/* стартовые */
html {
	box-sizing: border-box;
}

*,
*::before,
*::after {
	box-sizing: inherit;
}

body {
	background: #010001;
	min-width: 320px;
	color: #fff;
	font-size: 16px;
	line-height: 1;
	margin: 0;
	font-family: "Roboto", sans-serif;
}

button, input {
	font: inherit;
}

img {
	max-width: 100%;
	height: auto;
}

a {
	text-decoration: none;
	color: inherit;
	cursor: pointer;
}

ul {
	list-style: none;
	padding: 0;
	margin: 0;
}

h1, h2, h3, p {
	padding: 0;
	margin: 0;
}

.container {
	max-width: 1280px;
	margin: 0 auto;
	padding: 0 25px;
}


.list {
	margin: 0;
	padding: 0;
	display: flex;
	list-style: none;
}

.item-link {
	color: #ffffff;
	text-decoration: none;
	margin-left: 10px;
	margin-right: 10px;
	font-size: 12px;
	line-height: 14px;
}

/* header */


/* main */



/* features */

.features {
	position: relative;
	max-width: 1230px;
	width: 90%;
	margin: auto;
	padding: 100px 0;
}

.features-title {
	font-weight: bold;
	font-size: 28px;
	line-height: 140%;
	text-transform: uppercase;
	margin-bottom: 80px;
}

.table {
	margin-bottom: 100px;
}

.table-title, .features-subtitle {
	font-weight: bold;
	font-size: 24px;
	line-height: 140%;
	text-transform: uppercase;
	margin-bottom: 40px;
	text-align: left;
}

.table-column {
	width: 300px;
	padding-top: 7px;
	padding-bottom: 7px;
	font-style: normal;
	font-weight: normal;
	font-size: 18px;
	line-height: 179.69%;
}

.features-text {
	max-width: 490px;
	font-style: normal;
	font-weight: normal;
	font-size: 16px;
	line-height: 29px;
}

.rocket {
	position: absolute;
	top: 100px;
	right: 0;
	z-index: 99;
	max-width: 300px;
}

@media (max-width: 960px) {
	.rocket {
		max-width: 230px;
	}

	.table-column {
		width: 250px;
	}
}

@media (max-width: 768px) {
	.rocket {
		display: none;
	}
}

@media (max-width: 576px) {
	.rocket {
		position: static;
		margin-bottom: 30px;
	}

	.table {
		margin-bottom: 0;
	}

	.table-column {
		font-size: 14px;
		width: 150px;
	}
}

@media (max-width: 576px) {

	.features {
		padding-top: 30px;
		padding-bottom: 0;
	}
}


/* footer */



/* calendar */


/* launches */

.launches {

	width: 360px;
	height: 100%;
	border: 1px white solid;
	border-radius: 30px;
	background-color: #111111;
	overflow: hidden;
	padding: 30px;
	text-align: left;
}

.launches-date {
	font-size: 14px;
	margin-bottom: 15px;
	display: block;
}

.launches-image {
	position: relative;
}

.launches-content {
	overflow: hidden;
}


.launches-image img {

	object-fit: cover;
	display: block;
	margin: 0 auto;
}

.launches-title {
	margin-top: 25px;
	margin-bottom: 25px;
}

.launches-details {
	display: block;
}

@media (max-width: 425px) {


	.launches {
		width: 310px;
	}
}

